export default {
	data() {
		return {
			selectStaff: false,
			TabCur: 0,
			tagList: ['预约完成', '预约取消'],
			orderList: [],
			page: 1,
			totalPage: null
		};
	},
	onReachBottom() {
		if(this.page < this.totalPage){
			this.page += this.page
			this.GetOrderList()
		}
	},
	onLoad() {
		this.GetOrderList()
	},
	methods:{
		SendGift(){
			uni.showToast({
				icon: 'none',
				title: '请等待版本更新'
			})
		},
		book(e){
			console.log(e.currentTarget.dataset);
			uni.navigateTo({
				url: '../people-detail/people-detail?id=' + e.currentTarget.dataset.staffid
			})
		},
		GetOrderList(){
			var that = this
			uni.showLoading({
				title: '请稍等'
			})
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'order/list',
				data: {
					page: that.page,
					size: 10,
					merchantId: that.MerchantId
				},
				success(res) {
					if(res.data.code == 0){
						that.totalPage = res.data.data.totalPage
						for(var i in res.data.data.list){
							that.orderList.push(res.data.data.list[i])
						}
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
					uni.hideLoading()
				}
			})
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			if(this.TabCur == 0){
				this.selectStaff = false
			} else {
				this.selectStaff = true
			}
		}
	}
}
